import React, { createContext, useContext, useMemo } from "react";

import useVendorValidity from "../hooks/configuration/useVendorValidity";

export const VendorValidity = createContext();
export const useVendorValidityStore = () => useContext(VendorValidity);

export const VendorValidityProvider = ({ children }) => {
  const [{ vendorValidity }, validationDispatch] = useVendorValidity();

  const vendorValidationStatus = useMemo(() => {
    return vendorValidity;
  }, [vendorValidity]);

  return (
    <VendorValidity.Provider value={{ vendorValidationStatus, validationDispatch }}>
      {children}
    </VendorValidity.Provider>
  );
};
