import Cloud from "../components/cloud";
import Scenarios from "../components/cloud/dedup/Scenarios";
import Scenario from "../components/cloud/dedup/Scenario";
import Duplicates from "../components/cloud/dedup/duplicates/Duplicates";

import Configuration from "../components/cloud/configuration";
import StartPageSwitcher from "../components/cloud/configuration/StartPageSwitcher";
import EmptyLandingPage from "../components/cloud/configuration/EmptyLandingPage";
import BasicConfiguration from "../components/cloud/configuration/basicConfiguration";
import AdvancedConfiguration from "../components/cloud/configuration/advancedConfiguration";

import {
  DEDUP_PATH,
  ROUTE_CONFIGURATION,
  ROUTE_BASIC_CONFIGURATION,
  ROUTE_CLOUD_CLIENT,
  ROUTE_ADVANCED_CONFIGURATION,
  ROUTE_EMPTY_CONFIGURATION
} from "./addresses";

const routes = [
  {
    path: ROUTE_CLOUD_CLIENT,
    component: Cloud,
    routes: [
      {
        path: ROUTE_CONFIGURATION,
        component: Configuration,
        routes: [
          {
            exact: true,
            path: ROUTE_CONFIGURATION,
            component: StartPageSwitcher
          },
          {
            exact: true,
            key: ROUTE_BASIC_CONFIGURATION,
            path: ROUTE_BASIC_CONFIGURATION,
            component: BasicConfiguration
          },
          {
            exact: true,
            key: ROUTE_ADVANCED_CONFIGURATION,
            path: ROUTE_ADVANCED_CONFIGURATION,
            component: AdvancedConfiguration
          },
          {
            exact: true,
            key: ROUTE_EMPTY_CONFIGURATION,
            path: ROUTE_EMPTY_CONFIGURATION,
            component: EmptyLandingPage
          }
        ]
      },
      {
        exact: true,
        key: "scenarios",
        path: `${DEDUP_PATH}/scenarios`,
        component: Scenarios
      },
      {
        exact: true,
        key: "scenarios/details",
        path: `${DEDUP_PATH}/scenarios/:scenario_id`,
        component: Scenario
      },
      {
        exact: true,
        key: "scenarios/new",
        path: `${DEDUP_PATH}/scenarios/new`,
        component: Scenario
      },
      {
        exact: true,
        key: "duplicates/details",
        path: `${DEDUP_PATH}/duplicates/:scenario_id`,
        component: Duplicates
      }
    ]
  }
];

export default routes;
