import { useReducer, useState, useEffect } from "react";
import vendorValidityReducer from "../../reducers/configurations/vendorValidityReducer";
import { UPDATE_VENDOR_VALIDITY } from "../../consts/configurations/vendorValidityActions";
import fetchReducer, { fetchStatusReducer, initialState } from "../../reducers/fetchReducer";
import asyncAction from "../../lib/asyncAction";
import useInteractiveFetchStatus from "../useInteractiveFetchStatus";
import { useConfigurationsContext } from "../../contexts/configurationsContext";

import VendorConnection from "../../models/VendorConnection";
import { FETCH_IS_LOADING, FETCH_SET_DATA, FETCH_THROW_ERROR } from "../../consts/fetchActions";

import CloudConfig from "../../models/CloudConfig";

const useVendorValidity = () => {
  const [vendorValidity, validationDispatch] = useReducer(vendorValidityReducer, {});
  const [fetchState, fetchDispatch] = useReducer(fetchReducer, {
    isLoading: null,
    error: null,
    status: null
  });
  const { spinnerIsRunning, spinnerIsStopped } = useInteractiveFetchStatus()[1];

  useEffect(() => {
    if (fetchState.isLoading === true) {
      spinnerIsRunning("Validating vendor credentials");
    } else if (fetchState.isLoading === false) {
      spinnerIsStopped();
    }
  }, [fetchState.isLoading]);

  const validateVendors = async credentials => {
    (async () => {
      const validate = async () => {
        const vendorResults = {};

        const validations = credentials.map(async credentialsSet => {
          const vendorConnection = new VendorConnection(credentialsSet);
          return vendorConnection.validate().then(result => {
            const status = result.status === "failed" ? "failed" : "success";
            vendorResults[vendorConnection.type] = status;
          });
        });

        await Promise.all(validations);

        validationDispatch({
          payload: { ...vendorResults },
          type: UPDATE_VENDOR_VALIDITY
        });
      };
      await asyncAction(validate, fetchDispatch);
    })();
  };

  return [{ vendorValidity, fetchState }, validateVendors];
};

export const useVendorValidityNeedTobeMerged = () => {
  const [credentials, validateVendors] = useState(null);
  const [fetchState, fetchDispatch] = useReducer(fetchStatusReducer, initialState);
  const trueConfiguration = useConfigurationsContext()[0];

  useEffect(() => {
    if (credentials === null) return;
    fetchDispatch({
      type: FETCH_IS_LOADING,
      payload: { isLoading: true }
    });

    (async () => {
      const vendorResults = {};
      const invalidCredentials = [];
      const duplicateOrgNames = [];

      const validations = credentials.map(async credentialsSet => {
        const vendorConnection = new VendorConnection(credentialsSet);
        return vendorConnection.validate().then(result => {
          const vendorIdentifier = credentialsSet.label || credentialsSet.type;
          vendorResults[vendorIdentifier] = result;

          if (result.status === "failed") {
            invalidCredentials.push(vendorIdentifier);
          } else if (credentialsSet.type === "salesforce") {
            const cloudConfig = new CloudConfig(trueConfiguration);
            const duplicate = cloudConfig.validateUniqueDataSourceName(result);

            if (duplicate) {
              duplicateOrgNames.push(duplicate);
            }
          }
        });
      });
      await Promise.all(validations);

      if (invalidCredentials.length > 0 || duplicateOrgNames.length > 0) {
        fetchDispatch({
          type: FETCH_THROW_ERROR,
          payload: {
            error: { invalidCredentials, duplicateOrgNames }
          }
        });
      } else {
        fetchDispatch({
          type: FETCH_SET_DATA,
          payload: { data: vendorResults }
        });
      }

      fetchDispatch({ type: FETCH_IS_LOADING, payload: { isLoading: false } });
    })();
  }, [credentials, validateVendors]);

  return [fetchState, validateVendors];
};

export default useVendorValidity;
