import GraxResourceNotFoundError from "./errors/GraxResourceNotFoundError";
import GraxUnprocessibleEntityError from "./errors/GraxUnprocessibleEntityError";
import FetchFailureError from "./errors/FetchFailureError";

class GraxClient {
  get hostname() {
    return window.location.origin;
  }

  get sfdcorgid() {
    return window.__grax__.orgid;
  }

  get basicAuthToken() {
    if (window.__grax__.pageParams) {
      return window.__grax__.pageParams.authorization;
    } else {
      return null;
    }
  }

  _createHeaders() {
    return {
      sfdcorgid: this.sfdcorgid,
      Authorization: "Basic " + this.basicAuthToken,
      "content-type": "application/json"
    };
  }

  fetch(endpoint, options) {
    const headers = this._createHeaders();
    return fetch(this.hostname + endpoint, { ...options, headers })
      .then(async response => {
        if (response.ok) {
          return response;
        } else {
          if (response.status === 404) {
            throw new GraxResourceNotFoundError(`${response.status} (${response.statusText})`);
          } else if (response.status === 422) {
            const details = await response.json();
            throw new GraxUnprocessibleEntityError({
              message: `${response.status} (${response.statusText})`,
              details
            });
          } else {
            throw new Error(`${response.status} (${response.statusText})`);
          }
        }
      })
      .then(response => response.json())
      .catch(err => {
        if (err.message === "Network request failed") {
          throw new FetchFailureError({ ...err });
        }
        throw err;
      });
  }
}

export default GraxClient;
