import { useEffect } from "react";

import CloudConfig from "../../models/CloudConfig";

import {
  VENDORS_SF,
  VENDORS_EINSTEIN_SF_DESTINATION,
  VENDORS_BULK_RESTORE_SF_DESTINATION
} from "../../consts/configurations/vendors";

import { useConfigurationsContext } from "../../contexts/configurationsContext";
import { useSalesforceConnectionContext } from "../../contexts/salesforceConnectionContext";
import { useMetadataBackupContext } from "../../contexts/metadataBackupContext";
import { useSyncAnalyticsContext } from "../../contexts/syncAnalyticsContext";
import { useEnvironmentVariablesContext } from "../../contexts/environmentVariablesContext";
import { useCloudStorageContext } from "../../contexts/cloudStorageContext";
import useValidateAndSaveConfiguration from "./useValidateAndSaveConfiguration";
import useSelectedProvider from "./useSelectedProvider";

const useSaveAdvancedConfiguration = () => {
  const [sourceOfTrue, sourceOfTrueDispatch] = useConfigurationsContext();
  const [storageState] = useCloudStorageContext();
  const [metadataBackup] = useMetadataBackupContext();
  const [syncAnalytics] = useSyncAnalyticsContext();
  const [environmentVariables] = useEnvironmentVariablesContext();
  const [dataSources] = useSalesforceConnectionContext();
  const sfdcDestinations = useSelectedProvider(dataSources);
  const storage = useSelectedProvider(storageState);

  const [
    { success, data },
    { saveConfiguration, validateVendors }
  ] = useValidateAndSaveConfiguration(sourceOfTrueDispatch);

  useEffect(() => {
    if (success === true) {
      const cloudConfig = new CloudConfig(sourceOfTrue);
      cloudConfig.storage = storage;

      const { bulkRestoreSalesforceDestination, einsteinSalesforceDestination } = data;
      if (bulkRestoreSalesforceDestination) {
        cloudConfig.bulkRestoreDestinationDataSource = bulkRestoreSalesforceDestination;
      }
      if (einsteinSalesforceDestination && syncAnalytics.isEinsteinAnalyticsEnabled === true) {
        cloudConfig.syncSalesforceEinsteinDataSources = einsteinSalesforceDestination;
      }

      cloudConfig.syncAnalytics = syncAnalytics;
      cloudConfig.metadataBackup = metadataBackup;
      cloudConfig.timeMachine = environmentVariables.timeMachine;
      cloudConfig.pushToQueue = environmentVariables.pushToQueue;
      cloudConfig.variables = environmentVariables.variables;
      saveConfiguration(cloudConfig.configuration);
    }
  }, [success, data]);

  const onSave = () => {
    const validationRules = [
      {
        type: VENDORS_SF,
        label: VENDORS_BULK_RESTORE_SF_DESTINATION,
        credentials: sfdcDestinations.bulkRestoreSalesforceDestination
      }
    ];

    if (syncAnalytics.isEinsteinAnalyticsEnabled === true) {
      validationRules.push({
        type: VENDORS_SF,
        label: VENDORS_EINSTEIN_SF_DESTINATION,
        credentials: sfdcDestinations.einsteinSalesforceDestination
      });
    }

    // TODO: Reinstate after demo
    validateVendors([]);
  };
  return onSave;
};

export default useSaveAdvancedConfiguration;
