import _ from "lodash";

import vendorCredentialWhitelist from "../consts/configurations/vendorCredentialWhitelist.json";
import CloudConfig from "./CloudConfig";

class ConfigurationSummary {
  constructor(configuration) {
    const { elastic, storage, salesforce } = this._getEnvironmentFromConfiguration(configuration);
    this.salesforce = salesforce;
    this.storage = storage;
    this.elastic = elastic;
  }

  /**
   * Returns displayable form of vendor credentials
   *
   * @readonly
   * @memberof ConfigurationSummary
   */
  createSummaryData() {
    const dataTypes = ["salesforce", "elastic", "storage"];
    const results = {};

    dataTypes.forEach(type => {
      results[type] = this._displayableProperties(type);
    });

    return results;
  }

  /**
   * Converts vendor credentials into displayable format for Configuration Summary page,
   * including filtering out secure credentials, substituting readable names for json values,
   * and capitalizing properly.
   *
   * Display order is determined by vendor credentials whitelist file
   * @param { String } type type of vendor, to determine appropriate whitelist subsection
   * @returns { Object } formatted and filtered vendor credentials
   *
   * @memberof ConfigurationSummary
   */
  _displayableProperties(type) {
    const displayCredentials = [];

    const userCredentials = { ...this[type] };
    const userKeys = Object.keys(userCredentials);

    // get appropriate part of whitelist
    const whitelistGetter = type === "salesforce" ? "dataSource" : type;
    const whitelist = vendorCredentialWhitelist[whitelistGetter];

    const whitelistedCredentials = whitelist.map(entry => entry.jsonName);
    whitelistedCredentials.forEach(credential => {
      if (userKeys.includes(credential)) {
        const translationPair = whitelist.filter(entry => entry.jsonName === credential)[0];
        const label = translationPair.displayName;
        const content = this._formatContent(credential, userCredentials[credential]);

        displayCredentials.push({ label, content, truncate: true });
      }
    });
    return displayCredentials;
  }

  _formatContent(credential, value) {
    if (credential === "provider") {
      return value === "aws" ? "AWS" : _.upperFirst(value);
    }
    return _.toString(value);
  }

  _getEnvironmentFromConfiguration(configuration) {
    const cloudConfig = new CloudConfig(configuration);
    const { elastic, storage } = cloudConfig;
    const salesforce = cloudConfig.sourceDataSource;
    return { elastic, storage, salesforce };
  }
}

export default ConfigurationSummary;
