import React from "react";

import { ExpandableSection } from "@salesforce/design-system-react";

import { ADVANCED_CONFIG_SET_VALUE } from "../../../../../consts/configurations/configurationsActions";
import GitIntegration from "./GitIntegration";
import { useMetadataBackupContext } from "../../../../../contexts/metadataBackupContext";
import StatefulCheckbox from "../../../../commons/StatefulCheckbox";
import {
  useHandleInput,
  useHandleCheckbox,
  useHandleButtonGroup
} from "../../../../../hooks/useHandleForm";

const MetadataBackupSettings = () => {
  const [state, dispatch] = useMetadataBackupContext();
  const onBlur = useHandleInput(ADVANCED_CONFIG_SET_VALUE, dispatch);
  const handleButtonGroup = useHandleButtonGroup(ADVANCED_CONFIG_SET_VALUE, dispatch);
  const handleCheckbox = useHandleCheckbox(ADVANCED_CONFIG_SET_VALUE, dispatch);

  const {
    isEnabled,
    sourceControlEnabled,
    sourceControlProvider,
    gitRepositoryName,
    gitRepoAuthorizationToken,
    gitUserId
  } = state;

  const isGitEnableCheckbox = isEnabled === true && (
    <div className="slds-col slds-size_1-of-3">
      <StatefulCheckbox
        label="Enable Git Integration"
        id="sourceControlEnabled"
        checked={sourceControlEnabled}
        isDestructive
        variant="toggle"
        onToggle={handleCheckbox}
      />
    </div>
  );

  const gitIntegrationForm = isEnabled === true && sourceControlEnabled === true && (
    <GitIntegration
      gitRepositoryName={gitRepositoryName}
      gitRepoAuthorizationToken={gitRepoAuthorizationToken}
      gitUserId={gitUserId}
      sourceControlProvider={sourceControlProvider}
      handleButtonGroup={handleButtonGroup}
      onBlur={onBlur}
    />
  );

  return (
    <ExpandableSection
      title="Metadata Backup Settings"
      className="slds-m-bottom_medium slds-m-top_none"
    >
      <div className="slds-grid slds-gutters_small slds-m-bottom_x-small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulCheckbox
            label="Enable Metadata Backup"
            helpText="By default, metadata will always be backed up to the configured cloud storage provider."
            id="isEnabled"
            checked={isEnabled}
            isDestructive
            onToggle={handleCheckbox}
          />
        </div>
        {isGitEnableCheckbox}
      </div>
      {gitIntegrationForm}
    </ExpandableSection>
  );
};
export default MetadataBackupSettings;
