import { UPDATE_VENDOR_VALIDITY } from "../../consts/configurations/vendorValidityActions";
import InvalidActionTypeError from "../../models/errors/InvalidActionTypeError";

const vendorValidityReducer = (state, { payload, type }) => {
  switch (type) {
    case UPDATE_VENDOR_VALIDITY:
      return { ...state, ...payload };
    default:
      throw new InvalidActionTypeError();
  }
};

export default vendorValidityReducer;
