import React, { useState } from "react";

import { Button } from "@salesforce/design-system-react";
import RevealSectionWarning from "./RevealSectionWarning";
import ControlledExpandableSection from "../../../../commons/ControlledExpandableSection";

//  TODO: add display name to HOC
const withProtectedExpandableSection = heading => WrappedComponent => {
  return function EnableWrappedComponentHook({ ...props }) {
    const [isSectionRevealed, setIsSectionRevealed] = useState(false);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [wasModalOpen, setWasModalOpen] = useState(false);

    const onConfirm = () => {
      setIsSectionRevealed(true);
      setIsModalOpen(false);
      if (!wasModalOpen) setWasModalOpen(true);
    };

    const toggleRevealValuesModal = () => {
      if (isSectionRevealed === false && wasModalOpen === false) setIsModalOpen(true);
      else setIsSectionRevealed(!isSectionRevealed);
    };

    return (
      <ControlledExpandableSection title={heading}>
        <Button
          iconCategory="utility"
          iconVariant="bare"
          iconName={isSectionRevealed ? "hide" : "preview"}
          iconPosition="left"
          onClick={toggleRevealValuesModal}
          variant="base"
          label={isSectionRevealed ? "Hide Fields" : "Reveal Fields"}
          className="slds-m-top_x-small"
        />
        {isSectionRevealed === true && (
          <ul className="slds-m-top_x-small">
            <WrappedComponent {...props} />
          </ul>
        )}
        <RevealSectionWarning
          isOpen={isModalOpen}
          onRequestClose={() => setIsModalOpen(false)}
          confirm={onConfirm}
        />
      </ControlledExpandableSection>
    );
  };
};

export default withProtectedExpandableSection;
