import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";

import { ExpandableSection } from "@salesforce/design-system-react";
import StatefulCheckbox from "../../../../commons/StatefulCheckbox";

const propTypes = {
  // eslint-disable-next-line react/require-default-props
  cdcEnabled: PropTypes.string,
  handleCheckbox: PropTypes.func.isRequired
};

const ChangeDataCapture = ({ cdcEnabled, handleCheckbox }) => {
  const [value, setValue] = useState();

  useEffect(() => {
    if (cdcEnabled === "true" || cdcEnabled === true) setValue(true);
    else setValue(false);
  }, [cdcEnabled]);

  return (
    <ExpandableSection title="Change Data Capture" className="slds-m-bottom_medium slds-m-top_none">
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulCheckbox
            label="Enable Change Data Capture"
            helpText="This must be enabled for Change Data Capture to work.
            Please note you also must enable for each specific object within Salesforce's Change Data Capture setup.
            Ensure you do not have a GRAX Time Machine Apex
            Trigger enabled on any objects for which you have enabled CDC."
            checked={value}
            id="cdcEnabled"
            onToggle={handleCheckbox}
          />
        </div>
      </div>
    </ExpandableSection>
  );
};

ChangeDataCapture.propTypes = propTypes;

export default ChangeDataCapture;
