import _ from "lodash";

import GraxClient from "./GraxClient";
import GraxResourceNotFoundError from "./errors/GraxResourceNotFoundError";
import { DEFAULT_ENVIRONMENT_NAME } from "../consts/configurations/defaultEnvironmentName";
import { SOURCE_GLOBAL_IDS } from "../consts/configurations/sourceGlobalIds";
import { ENDPOINT_CONFIGURATION } from "../consts/addresses";
import { SALESFORCE_GIT_INTEGRATION_GITLAB } from "../consts/configurations/sfdcGitIntegration";
import {
  PUSH_TO_QUEUE_PROVIDER_APIGEE,
  PUSH_TO_QUEUE_PROVIDER_KAFKA
} from "../consts/configurations/pushToQueueProvider";

class CloudConfig {
  constructor(configuration = {}) {
    this.graxClient = new GraxClient();

    // use _.merge to deep-clone configuration object, since configuration has many levels
    // and is often handed in from state
    this.configuration = _.merge({}, configuration);
  }

  get environment() {
    if (!this.configuration || !this.configuration.environments) {
      this.configuration = {
        ...this.configuration,
        environments: { [DEFAULT_ENVIRONMENT_NAME]: { dataSources: [], elastic: [], storage: [] } }
      };
    }
    const key = Object.keys(this.configuration.environments)[0];
    return this.configuration.environments[key];
  }

  get globals() {
    if (!this.configuration || !this.configuration.globalEnvironmentDefaults) {
      this.configuration = {
        ...this.configuration,
        globalEnvironmentDefaults: {
          asyncAttachmentProcessorOrganizationIds: [],
          asyncBulkLoadOrganizationIds: [],
          asyncQueueOrganizationIds: [],
          syncSalesforceEinsteinOrganizationIds: [],
          syncSalesforceSourceOrganizationIds: [],
          metadataBackupOrganizationIds: [],
          odataOrganizationIds: []
        }
      };
    }

    return this.configuration.globalEnvironmentDefaults;
  }

  get bulkRestoreDestinationDataSource() {
    return this._findDataSourceByOrgId(this.globals.bulkRestoreDestinationOrganizationId);
  }

  set bulkRestoreDestinationDataSource(newSource) {
    this._addOrReplaceDataSource(newSource, ["bulkRestoreDestinationOrganizationId"]);
  }

  get elastic() {
    return this.environment.elastic[0];
  }

  /**
   * Set elastic environment to provided array
   * Accepts array containing new elastic configuration
   *
   * @param { Array } elasticConfig new elastic configuration
   * @memberof CloudConfig
   */
  set elastic(elasticConfig) {
    this.environment.elastic = [elasticConfig];
  }

  get metadataBackup() {
    return this.environment.metadataBackup[0];
  }

  /**
   * Set metadata Backup environment to provided array
   * Accepts array containing new elastic configuration
   *
   * @param { Array } elasticConfig new elastic configuration
   * @memberof CloudConfig
   */

  set metadataBackup(metadataBackup) {
    if (metadataBackup.isEnabled === undefined || metadataBackup.isEnabled === false) {
      const { name, isEnabled } = metadataBackup;
      this.environment.metadataBackup = [
        {
          name,
          isEnabled
        }
      ];
    } else if (
      metadataBackup.sourceControlEnabled === undefined ||
      metadataBackup.sourceControlEnabled === false
    ) {
      const { name, isEnabled, sourceControlEnabled } = metadataBackup;
      this.environment.metadataBackup = [
        {
          name,
          isEnabled,
          sourceControlEnabled
        }
      ];
    } else if (metadataBackup.sourceControlProvider === SALESFORCE_GIT_INTEGRATION_GITLAB) {
      const { gitUserId, ...omittedGitUserId } = metadataBackup;
      this.environment.metadataBackup = [omittedGitUserId];
    } else {
      this.environment.metadataBackup = [metadataBackup];
    }
  }

  get pushToQueue() {
    return this.environment.pushToQueue[0];
  }

  set pushToQueue(pushToQueue) {
    if (pushToQueue.provider === PUSH_TO_QUEUE_PROVIDER_KAFKA) {
      const { provider, authorizationToken, apiEndpoint } = pushToQueue;
      this.environment.pushToQueue = [{ provider, authorizationToken, apiEndpoint }];
    } else if (pushToQueue.provider === PUSH_TO_QUEUE_PROVIDER_APIGEE) {
      const { provider, apiClientId, apiEndpoint, apiClientSecret } = pushToQueue;
      this.environment.pushToQueue = [{ provider, apiClientId, apiEndpoint, apiClientSecret }];
    } else {
      this.environment.pushToQueue = [{}];
    }
  }

  get sourceDataSource() {
    return this._findDataSourceByOrgId(this.globals.asyncAttachmentProcessorOrganizationIds[0]);
  }

  /**
   * Set source data source to provided configuration and update globals accordingly
   *
   * @param { Object } dataSourceConfig new source data source configuration
   * @memberof CloudConfig
   */
  set sourceDataSource(newSource) {
    this._addOrReplaceDataSource(newSource, SOURCE_GLOBAL_IDS, true);
  }

  get syncSalesforceEinsteinOrganizationId() {
    return (this.globals.syncSalesforceEinsteinOrganizationIds || [])[0];
  }

  get syncSalesforceEinsteinDataSources() {
    return (
      (this.globals.syncSalesforceEinsteinOrganizationIds || []).map(id =>
        this._findDataSourceByOrgId(id)
      )[0] || null
    );
  }

  /**
   * Add a new sync Salesforce Einstein data source
   *
   * @param { Object } newSource new data source to add
   * @memberof CloudConfig
   */
  set syncSalesforceEinsteinDataSources(newSource) {
    this._addOrReplaceDataSource(newSource, ["syncSalesforceEinsteinOrganizationIds"], true);
  }

  get storage() {
    return this.environment.storage[0];
  }

  /**
   * Set storage environment to provided array
   * Accepts array containing new storage configuration
   *
   * @param { Array } storage new storage configuration
   * @memberof CloudConfig
   */
  set storage(storageConfig) {
    this.environment.storage = [storageConfig];
  }

  get syncAnalytics() {
    return this.environment.syncAnalytics[0];
  }

  /**
   * Set syncAnalytics environment to provided array
   * Accepts array containing new elastic configuration
   *
   * @param { Array } elasticConfig new elastic configuration
   * @memberof CloudConfig
   */

  set syncAnalytics({ isEinsteinAnalyticsEnabled, isMarketingCloudEnabled, ...syncAnalytics }) {
    if (isEinsteinAnalyticsEnabled === undefined || isEinsteinAnalyticsEnabled === false) {
      const {
        syncEinsteinAnalyticsDatasetName,
        syncSalesforceObjects,
        ...omittedEEinsteinAnalytics
      } = syncAnalytics;

      const orgId = this.syncSalesforceEinsteinOrganizationId;
      if (orgId !== undefined)
        this.removeDataSourceFromGlobals(orgId, ["syncSalesforceEinsteinOrganizationIds"]);

      syncAnalytics = omittedEEinsteinAnalytics;
    }

    if (isMarketingCloudEnabled === false) {
      const {
        marketingCloudHost,
        marketingCloudUser,
        marketingCloudPassword,
        marketingCloudPort,
        ...omittedMMC
      } = syncAnalytics;
      syncAnalytics = omittedMMC;
    }

    this.environment.syncAnalytics = [syncAnalytics];
  }

  get timeMachine() {
    return this.environment.timeMachine[0];
  }

  set timeMachine(timeMachine) {
    this.environment.timeMachine = [timeMachine];
  }

  get variables() {
    return this.environment.variables;
  }

  /**
   * Set variables environment to provided array
   * Accepts array containing new elastic configuration
   *
   * @param { Array } elasticConfig new elastic configuration
   * @memberof CloudConfig
   */

  set variables(variables) {
    if (variables.cdcEnabled !== undefined)
      this.environment.variables = { ...variables, cdcEnabled: variables.cdcEnabled.toString() };
    else {
      this.environment.variables = variables;
    }
  }

  /**
   * Return data sources with duplicate names
   *
   * @returns { Array }
   * @memberof CloudConfig
   */
  validateUniqueDataSourceName(newDataSource) {
    const duplicate = this.environment.dataSources.find(
      existingDataSource =>
        newDataSource.name === existingDataSource.name &&
        newDataSource.organizationId !== existingDataSource.organizationId
    );

    if (duplicate) {
      const { organizationId, name } = duplicate;
      return { organizationId, name };
    }
    return null;
  }

  /**
   * Removes a data source from specified globals. If data source is not used
   * in any other globals, removes data source.
   *
   * @param { String } orgId organization to remove
   * @param { Array } global global default(s) to remove from
   * @memberof CloudConfig
   */
  removeDataSourceFromGlobals(orgId, globals) {
    this._removeOrgIdFromGlobals(orgId, globals);
    if (this._findGlobalsByOrgId(orgId).length === 0) {
      this._removeDataSource(orgId);
    }
  }

  /**
   * Retrieves configuration from server
   *
   * @returns { Object } retrieved configuration or error
   * @memberof CloudConfig
   */
  async retrieve() {
    try {
      this.configuration = await this.graxClient.fetch(ENDPOINT_CONFIGURATION);
      return this.configuration;
    } catch (error) {
      if (error instanceof GraxResourceNotFoundError) {
        return {};
      }
      throw error;
    }
  }

  /**
   * Sends configuration to server for persistence
   *
   * @returns { Object } persisted configuration
   * @memberof CloudConfig
   */
  async save() {
    this.configuration = await this.graxClient.fetch(ENDPOINT_CONFIGURATION, {
      body: JSON.stringify(this.configuration),
      method: "POST"
    });
    return this.configuration;
  }

  /**
   * Overwrites any existing data source with same org id and updates global defaults if needed
   *
   * @param { Object } newSource source to be set
   * @param { Object } currentSource current source
   * @param { Array } globalKeysToSet keys for global defaults to set
   * @param { Boolean } removeExistingGlobals whether to remove organization id from globalKeysToSet
   * @memberof CloudConfig
   */
  _addOrReplaceDataSource(newSource, globalKeysToSet, overwriteGlobals = false) {
    const currentSource = this._findDataSourceByOrgId(newSource.organizationId);
    if (currentSource && currentSource.organizationId === newSource.organizationId) {
      const existingDataSourceIndex = this.environment.dataSources.indexOf(currentSource);
      this.environment.dataSources.splice(existingDataSourceIndex, 1, newSource);
    } else {
      this.environment.dataSources.push(newSource);
    }
    this._setGlobals(newSource.organizationId, globalKeysToSet, overwriteGlobals);
  }

  /**
   * Finds data source for provided org id
   *
   * @param { String } orgId
   * @returns { Object } data source or null
   * @memberof CloudConfig
   */
  _findDataSourceByOrgId(orgId) {
    const dataSources = this.environment.dataSources.filter(
      dataSource => dataSource.organizationId === orgId
    );
    return dataSources.length === 0 ? null : dataSources[0];
  }

  /**
   * Find all global defaults that reference the provided organization id
   *
   * @param { String } orgId to search by
   * @returns { Array } global defaults that include organization
   * @memberof CloudConfig
   */
  _findGlobalsByOrgId(orgId) {
    const globalNames = Object.keys(this.globals);
    return globalNames.filter(name => {
      if (name.endsWith("Ids")) {
        return this.globals[name].includes(orgId);
      }
      if (name.endsWith("Id")) {
        return this.globals[name] === orgId;
      }
      return false;
    });
  }

  /**
   * Removes data source with provided id from data sources array
   *
   * @param { String } orgId
   * @memberof CloudConfig
   */
  _removeDataSource(orgId) {
    const dataSourceToRemove = this.environment.dataSources.find(
      dataSource => dataSource.organizationId === orgId
    );
    const dataSourceIndex = this.environment.dataSources.indexOf(dataSourceToRemove);
    this.environment.dataSources.splice(dataSourceIndex, 1);
  }

  /**
   * Removes organization ID from specified global defaults
   *
   * @param { String } orgId
   * @param { Array } globals
   * @memberof CloudConfig
   */
  _removeOrgIdFromGlobals(orgId, globals) {
    globals.forEach(key => {
      if (key.endsWith("Ids")) {
        const index = this.globals[key].indexOf(orgId);
        if (index !== -1) this.globals[key].splice(index, 1);
      } else if (key.endsWith("Id")) {
        if (this.globals[key] === orgId) delete this.globals[key];
      }
    });
  }

  /**
   * Adds an id to globals for provided keys
   *
   * @param { String } id id to use in globals
   * @param { Array } globalKeys keys to set with new id
   * @memberof CloudConfig
   */
  _setGlobals(id, globalKeys, overwriteGlobals) {
    globalKeys.forEach(key => {
      if (key.endsWith("Ids")) {
        if (!this.globals[key]) this.globals[key] = [];
        overwriteGlobals ? (this.globals[key] = [id]) : this.globals[key].push(id);
      } else if (key.endsWith("Id")) {
        this.globals[key] = id;
      }
    });
  }
}

export default CloudConfig;
