import { useReducer, useEffect, useState } from "react";

import { CONFIGURATION_LOAD_INITIAL_STATE } from "../../consts/configurations/configurationsActions";
import { FETCH_IS_LOADING, FETCH_THROW_ERROR, FETCH_SET_DATA } from "../../consts/fetchActions";
import { INITIAL_STATE } from "../../consts/configurations/initialState";

import CloudConfig from "../../models/CloudConfig";
import asyncAction from "../../lib/asyncAction";
import configurationsReducer from "../../reducers/configurations/configurationsReducer";
import fetchReducer, { fetchStatusReducer, initialState } from "../../reducers/fetchReducer";

/**
 * Retrieves configuration from cloud
 *
 * @returns { Array } state (configuration and fetch), configuration dispatch function
 */
export const useConfigurationRetrieve = () => {
  const [configuration, configDispatch] = useReducer(configurationsReducer, INITIAL_STATE);
  const fetchDispatch = useReducer(fetchReducer, {
    isLoading: null,
    error: null,
    status: null
  })[1];

  useEffect(() => {
    (async () => {
      const retrieveConfig = async () => {
        const cloudConfig = new CloudConfig();
        await cloudConfig.retrieve();
        configDispatch({
          payload: cloudConfig.configuration,
          type: CONFIGURATION_LOAD_INITIAL_STATE
        });
      };
      await asyncAction(retrieveConfig, fetchDispatch);
    })();
  }, []);

  return [configuration, configDispatch];
};

/**
 * Saves configuration to cloud
 *
 * @returns { Array } state (configuration and fetch), configuration dispatch function
 */

export const useSaveConfiguration = () => {
  const [body, setBody] = useState(null);
  const [fetchState, fetchDispatch] = useReducer(fetchStatusReducer, initialState);

  useEffect(() => {
    if (body === null) return;
    fetchDispatch({
      type: FETCH_IS_LOADING,
      payload: { isLoading: true }
    });
    (async () => {
      try {
        const cloudConfig = new CloudConfig(body);
        await cloudConfig.save();
        fetchDispatch({
          type: FETCH_SET_DATA,
          payload: { data: cloudConfig.configuration }
        });
      } catch (error) {
        fetchDispatch({
          type: FETCH_THROW_ERROR,
          payload: { error: error.message }
        });
      }
      fetchDispatch({ type: FETCH_IS_LOADING, payload: { isLoading: false } });
    })();
  }, [body]);

  return [fetchState, setBody];
};
