import { useEffect } from "react";

import { CONFIGURATION_LOAD_INITIAL_STATE } from "../../consts/configurations/configurationsActions";
import VENDOR_DISPLAY_NAME_MAPPING from "../../consts/configurations/vendorDisplayNameMapping";
import useInteractiveFetchStatus from "../useInteractiveFetchStatus";
import { useSaveConfiguration } from "./useConfigurationFetch";
import { useVendorValidityNeedTobeMerged } from "./useVendorValidity";

const useValidateAndSaveConfiguration = sourceOfTrueDispatch => {
  const [validity, validateVendors] = useVendorValidityNeedTobeMerged();
  const [configState, saveConfiguration] = useSaveConfiguration();
  const [
    ,
    { spinnerIsRunning, spinnerIsStopped, successNotification, errorNotification }
  ] = useInteractiveFetchStatus();

  useEffect(() => {
    if (validity.isLoading === true || configState.isLoading === true) spinnerIsRunning();
    else if (configState.isLoading === false || validity.error !== null) spinnerIsStopped();
  }, [configState.isLoading, validity.isLoading, validity.error]);

  useEffect(() => {
    if (configState.success === true) successNotification("Configuration saved successfully");
  }, [configState.success]);

  useEffect(() => {
    if (configState.error !== null) errorNotification("Could not save configuration");
  }, [configState.error]);

  useEffect(() => {
    if (validity.error !== null && validity.isLoading === false) {
      let errorMessage = "";
      if (validity.error.duplicateOrgNames.length > 0) {
        errorMessage += "Salesforce connections must have unique names. ";
        validity.error.duplicateOrgNames.forEach(org => {
          const { organizationId, name } = org;
          errorMessage += `"${name}" is already used by connection ${organizationId}.`;
        });
        errorMessage += " Please rename your new connection and try again. ";
        errorNotification(errorMessage);
      }
      if (validity.error.invalidCredentials.length > 0) {
        const vendorList = validity.error.invalidCredentials
          .map(vendor => VENDOR_DISPLAY_NAME_MAPPING[vendor])
          .join(", ");
        errorMessage += `Invalid credentials: ${vendorList}`;
        errorNotification(errorMessage);
      }
    }
  }, [validity.error, validity.isLoading]);

  useEffect(() => {
    if (configState.success === true && configState.data !== null) {
      sourceOfTrueDispatch({
        type: CONFIGURATION_LOAD_INITIAL_STATE,
        payload: configState.data
      });
    }
  }, [configState.success, configState.data, sourceOfTrueDispatch]);

  return [validity, { saveConfiguration, validateVendors }];
};

export default useValidateAndSaveConfiguration;
