import { createRef } from "react";

import { useConfigurationsContext } from "../../contexts/configurationsContext";
import CloudConfig from "../../models/CloudConfig";

const useBasicConfiguration = () => {
  const state = useConfigurationsContext()[0];
  const formRef = createRef();

  const cloudConfig = new CloudConfig(state);
  const dataSources = {
    salesforceSource: cloudConfig.sourceDataSource
  };
  const elastic = cloudConfig.elastic;
  const storage = cloudConfig.storage;

  return [{ dataSources, elastic, storage, formRef }];
};

export default useBasicConfiguration;
