import {
  FETCH_SET_DATA,
  FETCH_IS_LOADING,
  FETCH_THROW_ERROR,
  FETCH_SET_STATUS
} from "../consts/fetchActions";
import InvalidActionTypeError from "../models/errors/InvalidActionTypeError";

export const initialState = {
  isLoading: null,
  error: null,
  data: null,
  success: null
};

const fetchReducer = (state, { type, payload }) => {
  switch (type) {
    case FETCH_SET_DATA:
    case FETCH_IS_LOADING:
    case FETCH_THROW_ERROR:
    case FETCH_SET_STATUS: {
      return { ...state, ...payload };
    }
    default:
      throw new InvalidActionTypeError();
  }
};

export const fetchStatusReducer = (state, { type, payload }) => {
  switch (type) {
    case FETCH_IS_LOADING:
      if (payload.isLoading === true) {
        return { ...initialState, isLoading: true };
      } else {
        return { ...state, isLoading: false };
      }
    case FETCH_SET_DATA:
      return { ...state, data: payload.data, error: null, success: true };
    case FETCH_THROW_ERROR:
      return { ...state, data: null, error: payload.error, success: false };
    case FETCH_SET_STATUS:
      return { ...state, ...payload };
    default:
      throw new InvalidActionTypeError();
  }
};

export default fetchReducer;
