import React from "react";
import PropTypes from "prop-types";

import { useHistory } from "react-router-dom";
import { Button, ButtonGroup } from "@salesforce/design-system-react";

import { ROUTE_CONFIGURATION } from "../../../consts/addresses";

const propTypes = {
  formRef: PropTypes.element.isRequired,
  onSubmit: PropTypes.func.isRequired
};

const HeaderButtons = ({ formRef, onSubmit }) => {
  const history = useHistory();

  const handleSubmit = () => {
    if (formRef.current.checkValidity() === true && typeof onSubmit === "function") onSubmit();
  };

  const goBack = () => history.push(ROUTE_CONFIGURATION);

  return (
    <ButtonGroup>
      <Button label="Cancel" onClick={goBack} />
      <Button label="Save" onClick={handleSubmit} type="submit" variant="brand" />
    </ButtonGroup>
  );
};

HeaderButtons.propTypes = propTypes;

export default HeaderButtons;
