import React from "react";
import PropTypes from "prop-types";
import { Alert } from "@salesforce/design-system-react";

const propTypes = {
  text: PropTypes.string.isRequired
};

const YellowWarning = ({ text }) => (
  <div className="slds-m-vertical_medium slds-m-horizontal_medium">
    <Alert
      className="slds-text-align_left"
      variant="warning"
      labels={{
        heading: text
      }}
    />
  </div>
);

YellowWarning.propTypes = propTypes;

export default YellowWarning;
