import {
  ELASTICSEARCH_PROVIDERS_BONSAI,
  ELASTICSEARCH_PROVIDERS_AWS,
  ELASTICSEARCH_PROVIDERS_ELASTIC_CO
} from "../../consts/configurations/elasticsearchProviders";
import {
  QUICK_START_FORM_SET_NAME,
  QUICK_START_FORM_SET_PROVIDER
} from "../../consts/configurations/configurationsActions";

import { useElasticsearchContext } from "../../contexts/elasticsearchContext";
import { useHandleInput, useHandleButtonGroup } from "../useHandleForm";

const options = [
  { label: "Bonsai Elasticsearch", value: ELASTICSEARCH_PROVIDERS_BONSAI },
  { label: "AWS Elasticsearch", value: ELASTICSEARCH_PROVIDERS_AWS },
  { label: "Elastic.co", value: ELASTICSEARCH_PROVIDERS_ELASTIC_CO }
];

/**
 * Accesses corresponding context to retrieve/set provider data
 *
 * @returns { Array } [{selectedProvider, provider, options}, {dispatch}]
 */
const useElasticsearchForm = () => {
  const [state, dispatch] = useElasticsearchContext();
  const onBlur = useHandleInput(QUICK_START_FORM_SET_NAME, dispatch);
  const handleButtonGroup = useHandleButtonGroup(QUICK_START_FORM_SET_PROVIDER, dispatch);
  const name = state.name;
  const selectedProvider = state.provider;
  const provider = state[selectedProvider];

  return [
    {
      name,
      selectedProvider,
      provider,
      options
    },
    { onBlur, handleButtonGroup, dispatch }
  ];
};

export default useElasticsearchForm;
