import React from "react";
import PropTypes from "prop-types";

import { QUICK_START_FORM_SET_PROVIDER } from "../../../consts/configurations/configurationsActions";
import { CONNECTION_PROVIDERS_SFDC } from "../../../consts/configurations/connectionProviders";

import { useVendorValidityStore } from "../../../contexts/VendorValidityContext";

import VendorValidityMessage from "../VendorValidityMessage";
import GRAXButtonGroup from "../ButtonGroup";
import StatefulInput from "../StatefulInput";
import SalesforceLogin from "./SalesforceLogin";
import OAuthLogin from "./OAuthLogin";

import useSalesforceConnection from "../../../hooks/configuration/useSalesforceConnection";

const propTypes = {
  showConnectionName: PropTypes.bool,
  variant: PropTypes.oneOf([
    "salesforceSource",
    "einsteinSalesforceDestination",
    "bulkRestoreSalesforceDestination"
  ]).isRequired
};

const SalesforceConnection = ({ variant, showConnectionName = false }) => {
  const [
    { userName, password, token, url, name, organizationId, options, selectedProvider },
    { onBlur, dispatch }
  ] = useSalesforceConnection(variant);
  const { vendorValidationStatus, validationDispatch } = useVendorValidityStore();

  const validateVendorCreds = () => {
    validationDispatch([
      {
        type: "salesforce",
        credentials: {
          provider: selectedProvider,
          userName,
          password,
          token,
          url,
          name,
          organizationId
        }
      }
    ]);
  };

  return (
    <>
      <VendorValidityMessage validity={{ salesforce: vendorValidationStatus.salesforce }} />
      {showConnectionName === true && (
        <div className="slds-grid slds-gutters_small slds-m-bottom_small">
          <div className="slds-col slds-size_2-of-3">
            <StatefulInput
              label="Connection Name"
              required
              value={name}
              id="name"
              helpText='Please name your Salesforce connection e.g. "Dev Sandbox“'
              onBlur={onBlur}
            />
          </div>
        </div>
      )}
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_2-of-3">
          <GRAXButtonGroup
            options={options}
            value={selectedProvider}
            onClick={provider =>
              dispatch({
                type: QUICK_START_FORM_SET_PROVIDER,
                payload: { provider }
              })
            }
          />
        </div>
      </div>
      {selectedProvider === CONNECTION_PROVIDERS_SFDC ? (
        <SalesforceLogin
          userName={userName}
          password={password}
          token={token}
          url={url}
          name={name}
          organizationId={organizationId}
          onBlur={onBlur}
          validationDispatch={validateVendorCreds}
        />
      ) : (
        <OAuthLogin />
      )}
    </>
  );
};

SalesforceConnection.propTypes = propTypes;

export default SalesforceConnection;
