import React from "react";
import PropTypes from "prop-types";
import _ from "lodash";
import { ScopedNotification, Icon } from "@salesforce/design-system-react";

import VENDOR_DISPLAY_NAME_MAPPING from "../../consts/configurations/vendorDisplayNameMapping";

const propTypes = PropTypes.shape({
  validity: PropTypes.objectOf(PropTypes.string)
});

const iconSettingsTypes = {
  success: {
    label: "success",
    category: "utility",
    colorVariant: "default",
    name: "success"
  },
  failed: {
    label: "failure",
    category: "utility",
    colorVariant: "error",
    name: "error"
  }
};

const VendorValidityMessage = ({ validity }) => {
  const provider = Object.keys(validity)[0];
  const messageType = validity[provider];
  if (messageType === undefined) return null;

  const displayProvider = VENDOR_DISPLAY_NAME_MAPPING[provider];

  const messages = {
    success: `${_.capitalize(displayProvider)} credentials validated`,
    failed: `Invalid ${displayProvider} credentials`
  };

  const iconSettings = iconSettingsTypes[messageType];
  const icon = (
    <Icon
      assistiveText={{
        label: iconSettings.label
      }}
      category={iconSettings.category}
      colorVariant={iconSettings.colorVariant}
      name={iconSettings.name}
      size="x-small"
    />
  );

  return (
    <div className="slds-grid slds-row slds-grid_align-center slds-m-bottom_x-small">
      <ScopedNotification icon={icon} theme="light">
        <p>{messages[messageType]}</p>
      </ScopedNotification>
    </div>
  );
};

VendorValidityMessage.propTypes = propTypes;

export default VendorValidityMessage;
