import React from "react";
import { Card } from "@salesforce/design-system-react";

import { SalesforceConnectionProvider } from "../../../../contexts/salesforceConnectionContext";
import { MetadataBackupProvider } from "../../../../contexts/metadataBackupContext";
import { EnvironmentVariablesProvider } from "../../../../contexts/environmentVariablesContext";
import { SyncAnalyticsProvider } from "../../../../contexts/syncAnalyticsContext";

import MetadataBackupSettings from "./metadataBackupSettings";
import SyncAnalytics from "./syncAnalytics";
import EnvironmentVariables from "./environmentVariables";
import HeaderActions from "./HeaderActions";

import useAdvancedConfiguration from "../../../../hooks/configuration/useAdvancedConfiguration";
import { VendorValidityProvider } from "../../../../contexts/VendorValidityContext";
import { CloudStorageProvider } from "../../../../contexts/cloudStorageContext";

const AdvancedConfiguration = () => {
  const {
    dataSources,
    storage,
    syncSalesforceEinsteinOrganizationId,
    metadataBackup,
    syncAnalytics,
    environmentVariables,
    formRef
  } = useAdvancedConfiguration();

  return (
    <MetadataBackupProvider value={metadataBackup}>
      <SalesforceConnectionProvider value={dataSources}>
        <SyncAnalyticsProvider value={syncAnalytics}>
          <EnvironmentVariablesProvider value={environmentVariables}>
            <CloudStorageProvider value={storage}>
              <VendorValidityProvider>
                <form ref={formRef} onSubmit={e => e.preventDefault()}>
                  <Card
                    heading="Advanced Configuration"
                    style={{ flexGrow: 1 }}
                    headerActions={<HeaderActions formRef={formRef} />}
                  >
                    <div className="slds-p-horizontal_medium">
                      <MetadataBackupSettings />
                      <SyncAnalytics
                        syncSalesforceEinsteinOrganizationId={syncSalesforceEinsteinOrganizationId}
                      />
                      <EnvironmentVariables />
                    </div>
                  </Card>
                </form>
              </VendorValidityProvider>
            </CloudStorageProvider>
          </EnvironmentVariablesProvider>
        </SyncAnalyticsProvider>
      </SalesforceConnectionProvider>
    </MetadataBackupProvider>
  );
};

export default AdvancedConfiguration;
