import React, { useState } from "react";

import { Combobox, comboboxFilterAndLimit } from "@salesforce/design-system-react";

const GRAXCombobox = ({ options = [], onChange, ...props }) => {
  const [inputValue, setInputValue] = useState("");
  const [selection, setSelection] = useState([]);

  const onChangeHandler = (event, dataSelection) => {
    if (onChange) {
      onChange(event, dataSelection, options);
    }
  };

  return (
    <Combobox
      events={{
        onChange: (event, { value }) => setInputValue(value),
        onRequestRemoveSelectedOption: (event, data) => {
          setInputValue("");
          setSelection(data.selection);

          onChangeHandler(event, data.selection);
        },
        onSelect: (event, data) => {
          setInputValue("");
          setSelection(data.selection);

          onChangeHandler(event, data.selection);
        }
      }}
      options={comboboxFilterAndLimit({
        inputValue: inputValue,
        limit: 1000,
        options: options,
        selection: selection
      })}
      selection={selection}
      value={inputValue}
      variant={"inline-listbox"}
      required={true}
      menuItemVisibleLength={5}
      classNameMenu="slds-dropdown_length-7"
      {...props}
    />
  );
};

export default GRAXCombobox;
