import { useState, useReducer, useEffect } from "react";

import {
  FETCH_SET_DATA,
  FETCH_IS_LOADING,
  FETCH_THROW_ERROR,
  FETCH_SET_STATUS
} from "../consts/fetchActions";

import Axios from "../lib/api";
import fetchReducer from "../reducers/fetchReducer";
/* TODO: Improve API request hook  */
const useFetch = ({ ...initialConfig }, initialState = {}) => {
  const [config, setConfig] = useState(initialConfig);
  const [state, dispatch] = useReducer(fetchReducer, {
    data: null,
    isLoading: null,
    error: null,
    status: null,
    ...initialState
  });

  useEffect(() => {
    if (config.url === undefined) return;
    dispatch({ type: FETCH_IS_LOADING, payload: { isLoading: true } });
    (async () => {
      try {
        const response = await Axios(config);
        dispatch({ type: FETCH_SET_DATA, payload: { data: response.data } });
        dispatch({ type: FETCH_SET_STATUS, payload: { status: response.status } });
      } catch (error) {
        dispatch({ type: FETCH_THROW_ERROR, payload: { error } });
        if (error.response !== undefined) {
          dispatch({ type: FETCH_SET_STATUS, payload: { status: error.response.status } });
        }
      }
    })();
    dispatch({ type: FETCH_IS_LOADING, payload: { isLoading: false } });
  }, [config]);

  return [state, setConfig];
};

export default useFetch;
