import React from "react";
import PropTypes from "prop-types";
import AwsRegionCombobox from "../../../../commons/AwsRegionCombobox";
import StatefulInput from "../../../../commons/StatefulInput";
import EnableOAuth from "./EnableOAuth";
import StatefulCheckbox from "../../../../commons/StatefulCheckbox";

const propTypes = {
  s3CompatibleEndpoint: PropTypes.string.isRequired,
  bucket: PropTypes.string.isRequired,
  accessKeyName: PropTypes.string.isRequired,
  secretAccessKey: PropTypes.string.isRequired,
  enableOAuth: PropTypes.string.isRequired,
  clientId: PropTypes.string.isRequired,
  tokenUrl: PropTypes.string.isRequired,
  clientSecret: PropTypes.string.isRequired,
  region: PropTypes.oneOf([]).isRequired,
  onBlur: PropTypes.func.isRequired,
  handleCheckbox: PropTypes.func.isRequired,
  handleCombobox: PropTypes.func.isRequired
};

const S3Compatible = ({
  s3CompatibleEndpoint,
  bucket,
  accessKeyName,
  secretAccessKey,
  enableOAuth,
  clientId,
  tokenUrl,
  clientSecret,
  region,
  onBlur,
  handleCheckbox,
  handleCombobox
}) => (
  <>
    <div className="slds-grid slds-gutters_small">
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Endpoint"
          required
          value={s3CompatibleEndpoint}
          id="s3CompatibleEndpoint"
          onBlur={onBlur}
        />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput label="S3 Bucket Name" required value={bucket} id="bucket" onBlur={onBlur} />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <AwsRegionCombobox
          label="S3 Region"
          value={region}
          onChange={selections => handleCombobox("region", selections)}
        />
      </div>
    </div>
    <div className="slds-grid slds-gutters_small">
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Access Key ID"
          required
          secured
          value={accessKeyName}
          id="accessKey"
          onBlur={onBlur}
        />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Secret Access Key"
          required
          secured
          value={secretAccessKey}
          id="secretAccessKey"
          onBlur={onBlur}
        />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulCheckbox
          label="Enable OAuth"
          checked={enableOAuth}
          id="enableOAuth"
          variant="toggle"
          isDestructive
          onToggle={handleCheckbox}
        />
      </div>
    </div>
    {enableOAuth === true && (
      <EnableOAuth
        clientId={clientId}
        tokenUrl={tokenUrl}
        clientSecret={clientSecret}
        onBlur={onBlur}
      />
    )}
  </>
);

S3Compatible.propTypes = propTypes;

export default S3Compatible;
