import { FETCH_IS_LOADING, FETCH_THROW_ERROR } from "../consts/fetchActions";

const asyncAction = async (func, fetchDispatch) => {
  fetchDispatch({ type: FETCH_IS_LOADING, payload: { isLoading: true } });
  try {
    await func();
    fetchDispatch({ type: FETCH_IS_LOADING, payload: { isLoading: false, success: true } });
  } catch (error) {
    fetchDispatch({
      type: FETCH_THROW_ERROR,
      payload: { error: error.message, isLoading: false, success: false }
    });
  }
};

export default asyncAction;
