import React, { Fragment } from "react";

import { ExpandableSection, Button } from "@salesforce/design-system-react";

import { useVendorValidityStore } from "../../../../../contexts/VendorValidityContext";
import GRAXButtonGroup from "../../../../commons/ButtonGroup";
import StatefulInput from "../../../../commons/StatefulInput";
import VendorValidityMessage from "../../../../commons/VendorValidityMessage";
import ProviderFactory from "./ProviderFactory";

import useElasticsearchForm from "../../../../../hooks/configuration/useElasticsearchForm";

const ElasticsearchForm = () => {
  const [
    { selectedProvider, name, provider, options },
    { handleButtonGroup, onBlur, dispatch }
  ] = useElasticsearchForm();
  const { vendorValidationStatus, validationDispatch } = useVendorValidityStore();

  const validateVendorCreds = () => {
    validationDispatch([{ type: "elastic", credentials: { ...provider } }]);
  };

  return (
    <Fragment>
      <ExpandableSection
        title="Elasticsearch Provider"
        className="slds-m-bottom_medium slds-m-top_none"
        nonCollapsible
      >
        <div className="slds-grid slds-gutters_small  slds-m-bottom_small">
          <div className="slds-col slds-size_2-of-3">
            <StatefulInput label="Name" required value={name} id="name" onBlur={onBlur} />
          </div>
        </div>
        <VendorValidityMessage validity={{ elastic: vendorValidationStatus.elastic }} />
        <div className="slds-grid slds-gutters_small">
          <div className="slds-col slds-size_2-of-3">
            <GRAXButtonGroup
              options={options}
              value={selectedProvider}
              id="provider"
              onClick={handleButtonGroup}
            />
          </div>
        </div>
        <ProviderFactory selectedProvider={selectedProvider} {...provider} dispatch={dispatch} />
        <div className="slds-grid slds-gutters_small slds-m-top_small">
          <div className="slds-col slds-size_1-of-3">
            <Button label="Validate Elastic Connection" onClick={validateVendorCreds} />
          </div>
        </div>
      </ExpandableSection>
    </Fragment>
  );
};

export default ElasticsearchForm;
