import React from "react";
import { Card } from "@salesforce/design-system-react";

import { SalesforceConnectionProvider } from "../../../../contexts/salesforceConnectionContext";
import { ElasticsearchProvider } from "../../../../contexts/elasticsearchContext";
import { CloudStorageProvider } from "../../../../contexts/cloudStorageContext";
import { VendorValidityProvider } from "../../../../contexts/VendorValidityContext";
import YellowWarning from "../../../commons/YellowWarning";
import SalesforceOrigin from "./SalesforceOrigin";
import ElasticSearchForm from "./elasticsearchForm";
import CloudStorageForm from "./cloudStorageForm";
import HeaderActions from "./HeaderActions";
import useBasicConfiguration from "../../../../hooks/configuration/useBasicConfiguration";

const warningText = `Ensure you have the correct credentials for ALL sections before beginning.
        Upon saving the page, a test will be made to verify each connection.
        You will not be able to save until all connections are validated.`;

const BasicCredentialsForm = () => {
  const [{ dataSources, elastic, storage, formRef }] = useBasicConfiguration();

  return (
    <VendorValidityProvider>
      <SalesforceConnectionProvider value={dataSources}>
        <ElasticsearchProvider value={elastic}>
          <CloudStorageProvider value={storage}>
            <form ref={formRef} onSubmit={e => e.preventDefault()}>
              <Card
                heading=""
                style={{ flexGrow: 1 }}
                headerActions={<HeaderActions formRef={formRef} />}
              >
                <YellowWarning text={warningText} />
                <div className="slds-p-horizontal_medium">
                  <SalesforceOrigin />
                  <ElasticSearchForm />
                  <CloudStorageForm />
                </div>
              </Card>
            </form>
          </CloudStorageProvider>
        </ElasticsearchProvider>
      </SalesforceConnectionProvider>
    </VendorValidityProvider>
  );
};

export default BasicCredentialsForm;
